import Tooltip from '@buzzguru/ui/Tooltip';
import Link from '@lskjs/link';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';

import getRequestParams from '../../../utils/getRequestParams';
import { MoreWrapper, Wrapper } from './Mention.styles';
import Avatar from '../../Avatar';

const TestAvatar = ({ src, title, size, cspStyle = {}, defaultAvatar }) => {
  const spanRef = useRef(null);
  useEffect(() => {
    // eslint-disable-next-line no-param-reassign
    cspStyle = {
      ...cspStyle,
      maxWidth: 'none',
    };
    const img = window.__lskjs.GM_addElement(spanRef.current, 'img', {
      src: src || defaultAvatar,
      alt: title,
      title,
      width: size,
      height: size,
    });
    Object.keys(cspStyle).forEach((key) => {
      if (typeof cspStyle[key] === 'number') {
        img.style[key] = `${String(cspStyle[key])}px`;
      } else {
        img.style[key] = cspStyle[key];
      }
    });
  }, []);
  return <span ref={spanRef}></span>;
};

const Mention = ({ entity, i18 }) => {
  const { item } = entity;
  const [type, setType] = useState('youtube');
  const appMentions = item?.mentionsApps || {};
  const gameMentions = item?.mentionsGames || {};
  const mentionsChannels = item?.mentionsChannels || [];
  // const siteMentions = item?.mentionsSites || {};
  useEffect(() => {
    getRequestParams().then((res) => setType(res.type || 'youtube'));
  }, []);
  return (
    <Wrapper>
      {(appMentions.items || []).length ? (
        <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}/products`}>
          <Tooltip id="apps-mentions" overlay={i18.t('productMentions.mentionOfProduct')}>
            {(appMentions?.items || [])
              .filter(({ product }) => (product.info?.image || '').length > 0)
              .slice(0, 3)
              .map(({ product }) => (
                <Avatar
                  id={product?.id}
                  key={product?.id}
                  size={36}
                  src={product?.info?.image}
                  defaultAvatar="https://buzzguru.com/assets/images/product.png"
                  shape="square"
                  innerStyle={{
                    borderRadius: 8,
                    border: '2px solid #313137',
                  }}
                  style={{
                    marginRight: -12,
                    background: 'none',
                  }}
                />
              ))}
            <MoreWrapper>{`+${appMentions?.totals?.total || 0}`}</MoreWrapper>
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
      {(gameMentions.items || []).length ? (
        <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}/games`}>
          <Tooltip id="games-mentions" overlay={i18.t('gamesMentions.mentionOfGame')}>
            {(gameMentions?.items || []).slice(0, 3).map(({ product }) => (
              <Avatar
                id={product?.id}
                key={product?.id}
                size={36}
                src={product?.info?.image}
                defaultAvatar="https://buzzguru.com/assets/images/product.png"
                shape="square"
                innerStyle={{
                  borderRadius: 8,
                  border: '2px solid #313137',
                }}
                style={{
                  marginRight: -12,
                  background: 'none',
                }}
              />
            ))}
            <MoreWrapper>{`+${gameMentions?.totals?.total || gameMentions?.totals?.totalUniqProductsCount || 0}`}</MoreWrapper>
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
      {(mentionsChannels || []).length ? (
          <>
            {(mentionsChannels || []).slice(0, 4).map((channel) => (
              <Tooltip id="channel-mentions" overlay={channel?.name}>
                <Link key={channel?._id} target="_blank" href={`https://buzzguru.com/${type}/channels/${channel?._id}`}>
                  <Avatar
                    title={channel?.name}
                    src={channel?.avatar}
                    defaultAvatar="https://bzzguru.com/assets/images/blogger.png"
                    size={36}
                    cspStyle={{ marginRight: -12, borderRadius: '50px', border: '2px solid #313137', }}
                    // style={{ marginRight: 12 }}
                  />
                </Link>
              </Tooltip>
            ))}
            <Tooltip id="channel-mentions-total" overlay={i18.t('videoSitesBlock.mentionAll')}>
              <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}`}>
                <MoreWrapper style={{ borderRadius: '50%', marginBottom: 4 }}>{`+${(mentionsChannels || []).length}`}</MoreWrapper>
              </Link>
            </Tooltip>
          </>
      ) : (
        ''
      )}
    </Wrapper>
  );
};

Mention.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
};

export default inject('i18')(Mention);
